/*switch*/
#testswitch[data-v-3107ae22] {  
        position: relative; border:0;
        float: right; height: 7vw;
        width: 50px!important;
        margin: 3.5vw 5vw 0 0; padding: 0;
}
.testswitch-checkbox[data-v-3107ae22] {  
        display: none;
}
.testswitch-label[data-v-3107ae22] {  
        display: block;   
        overflow: hidden;   
        cursor: pointer;  
        border: 1px solid #999999;   
        border-radius: 20px;
}
.testswitch-inner[data-v-3107ae22] {  
        display: block;   
        width: 200%;   
        margin-left: -100%;  
        transition: margin 0.3s ease-in 0s;
}  
      /*高度*/
.testswitch-inner[data-v-3107ae22]::before, .testswitch-inner[data-v-3107ae22]::after {  
        display: block;   
        float: right;   
        width: 50%;   
        height: 16px;   
        padding: 0;   
        line-height: 16px;  
        font-size: 12px;   
        color: white;   
        font-family:   
        Trebuchet, Arial, sans-serif;   
        font-weight: bold;  
        box-sizing: border-box;
}
.testswitch-inner[data-v-3107ae22]::after {  
        content: attr(data-on);  
        padding-left: 10px;  
        background-color: #00e500;   
        color: #FFFFFF;
}
.testswitch-inner[data-v-3107ae22]::before {  
        content: attr(data-off);  
        padding-right: 5px;  
        background-color: #EEEEEE;   
        color: #999999;  
        text-align: right;
}
.testswitch-switch[data-v-3107ae22] {  
        position: absolute;   
        display: block;   
        width: 12px;  
        height: 12px;  
        margin: 3px;  
        background: #FFFFFF;  
        top: 0;   
        bottom: 0;  
        right: 30px;  
        border: 1px solid #999999;   
        border-radius: 20px;  
        transition: all 0.3s ease-in 0s;
}
.testswitch-checkbox:checked + .testswitch-label .testswitch-inner[data-v-3107ae22] {  
        margin-left: 0;
}
.testswitch-checkbox:checked + .testswitch-label .testswitch-switch[data-v-3107ae22] {  
        right: 0px;
}  



