@-webkit-keyframes hover-color {
from {
    border-color: #c0c0c0;
}
to {
    border-color: #3e97eb;
}
}
@keyframes hover-color {
from {
    border-color: #c0c0c0;
}
to {
    border-color: #3e97eb;
}
}
.magic-radio[data-v-84b4594c]{
  position: absolute;
  display: none;
}
.magic-radio[disabled][data-v-84b4594c]{
  cursor: not-allowed;
}
.magic-radio + label[data-v-84b4594c]{
  position: relative;
  display: block; line-height: 9vw;
  padding-left: 12vw;
  cursor: pointer;
  vertical-align: middle;
}
.magic-radio + label[data-v-84b4594c]:hover:before{
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    -webkit-animation-name: hover-color;
            animation-name: hover-color;
}
.magic-radio + label[data-v-84b4594c]:before{
    position: absolute;
    top: 2vw;
    left: 5vw;
    display: inline-block;
    width: 5vw;
    height: 5vw;
    content: '';
    border: 0.3vw solid #c0c0c0;
}
.magic-radio + label[data-v-84b4594c]:after{
    position: absolute;
    display: none;
    content: '';
}
.magic-radio:checked + label[data-v-84b4594c]:before{
  -webkit-animation-name: none;
          animation-name: none;
}
.magic-radio:checked + label[data-v-84b4594c]:after{
  display: block;
}
.magic-radio + label[data-v-84b4594c]:before {
  border-radius: 50%;
}
.magic-radio + label[data-v-84b4594c]:after {
  top: 3vw;
  left: 6vw;
  width: 3vw;
  height: 3vw;
  border-radius: 50%;
  background: #3e97eb;
}
.magic-radio:checked + label[data-v-84b4594c]:before {
  border: 0.3vw solid #3e97eb;
}
.magic-radio:checked[disabled] + label[data-v-84b4594c]:before {
  border: 0.3vw solid #c9e2f9;
}
.magic-radio:checked[disabled] + label[data-v-84b4594c]:after {
  background: #c9e2f9;
}




